input,button { 
    &:focus{
      outline:none;
    }   
}
.bg-cover {
    background-size: cover;
    background-position: center center;
}
//
.dropnone {
    &::after {
        border:none;
    }
}
.bg-primary-o {
  background-color: rgba(theme-color('primary'), .75);
  &--2 {
    background-color: rgba(theme-color('primary'), .2);
    &:hover {
      background-color: rgba(theme-color('primary'), .3);
      text-decoration: none;
    }
  }
}

.bg-gradient {
  background-image:url(../images/bg-pattern.png), linear-gradient(to right, #2583c1, theme-color('primary'));
}
.bg-pattern {
  background-image: url(../images/bg-pattern.png);
  background-repeat: repeat;
}
.bg-tit {
  background-image:url(../images/bg-tit.jpg);
}
.bg-absolute {
  position: absolute;
  top: 0;
  bottom: 0;
  // left: 0;
}
.rounded-big {
  border-radius: 100px;
}
.badge-news {
  color: #fff;
  background-color: #23a4ff;
}
.badge-system {
  color: #fff;
  background-color: #35b1ed;
}
.btn-more {
  position: relative;
  &::after {
    content: '';
    width: 30px;
    height: 1px;;
    display: inline-block;
    vertical-align: middle;
    margin-left: 0.5em;
    transition: width .3s ease;
    background-color: theme-color('light');
  }
  &:hover {
    text-decoration: none;
    &::after {
      width: 50px;
    } 
  }
  &--primary {
    &::after {
      background-color: theme-color('primary');
    }
    
  }
}

.navbar-menu {
  position: absolute;
  z-index: 100;
  width: 100%;
}
.max-width-lg {
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
}
.custom-toggler.navbar-toggler {
  border-color: theme-color('primary');
}
.custom-toggler .navbar-toggler-icon {
background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(36, 63, 159, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}
.navbar-nav {
  .nav-link {
    color: #fff;
    position: relative;
    display: inline-block;
    &::before {
      content: '';
      width: 0%;
      height: 1px;
      background: #fff;
      position: absolute;
      right: 0px;
      bottom: 5px;
      left: inherit;
      transition: width .4s ease;
      }
    &:hover {
      &::before {
        width: 100%;
        left: 0px;
      }
    }
  }
  .show > .nav-link,
  .active > .nav-link,
  .nav-link.show,
  .nav-link.active {
    &::before {
      width: 100%;
      left: 0px;
    }
  }
}
.dropdown-menu {
  border-width:1px 0; 
  border-color: rgba(theme-color('light'), .75);
  text-align: center;
  @include media-breakpoint-up(lg) { 
    text-align: left;
    background-color: rgba(theme-color('light'), .75);
  }
}
.dropdown-item {
  color: theme-color('primary');
  padding-top: .5rem;
  padding-bottom: .5rem;
  transition: all .3s;
  &:hover {
    background-color: rgba(theme-color('primary'), .75);
    color: theme-color('light');
  }
}

.list-news {
  list-style: none;
  padding: 0;
  >li {
    line-height: 3rem;
    border-bottom: 1px solid theme-color('light');
    >a {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
}
.card-shadow {
  margin-bottom: 2em;
  transition: box-shadow .3s ease;
  &:hover {
    box-shadow: 0px 0px 10px #888888;
  }
}
.img-game {
  position: relative;
  &:hover {
    .img-hover {
      // visibility: inherit;
      opacity: 1;
    }
  }
}
.img-hover {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(theme-color('primary'), .75);
  // visibility: hidden;
  opacity: 0;
  transition: all .3s ease;
  >i {
    font-size: 4rem;
    line-height: 1em;
  }
  >p {
    font-size: 1.25rem;
  }
}
.icon-category {
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  background-color:theme-color('danger');
  &::after {
    position: absolute;
    top: 100%;
    right: 0;
    content: '';
    width:0;
    height:0;
    border-width:10px 25px 0px ;
    border-style:solid;
    border-color: theme-color('danger') transparent transparent ;
  }
}
.icon-group {
  a {
    width: 50px;
    height: 50px;
    font-size: 1.5rem;
    margin-left: 0.25rem;
    &:first-child {
      margin-left: 0;
    }
  }
  .flaticon-apk {
    line-height: 55px;
    font-size: 2rem;
    margin-left: -4px;
  }
  .flaticon-google-plus,
  .flaticon-gamepad-console {
    line-height: 35px;
    font-size: 2rem;
    margin-left: -4px;
  }
}
.img-128 {
  max-width: 128px;
  height: auto;
}
.img-35 {
  width: 35px;
  height: 35px;
}

.back-to-top {
  border-radius: 5px;
  position: fixed;
  padding: 5px 10px;
  background-color: theme-color('light');
  z-index: 200;
  bottom: 10px;
  right: 10px;
  transition: opacity .3s;
  opacity: .5;
  &:hover {
    opacity: 1;
  }
}

.mixitup-control.mixitup-control {
  color: theme-color('primary');
  >a {
    &:hover {
      color: theme-color('light');
    }
  }
}
a.mixitup-control-active.mixitup-control-active {
  background-color: theme-color('primary');
  color: theme-color('light'); 
}
.nav-page {
  >a:hover,>a.active{
    &::before {
      font-family: Flaticon;
      content: "\f112";
      font-size: 0.8rem;
      margin-right: .25rem;
    }
  }
}
.news-article {
  line-height: 2rem;
  // font-size: 1.1rem;
}

.collapse-content-nav {
  a {
    display: block;
    padding: 0.5rem 1rem;
    margin-bottom: 1px;
    &:hover,&.active {
      background-color: rgba(theme-color('primary'), .75);
      color: theme-color('light'); 
      text-decoration: none;
    }
  }
}
a[data-toggle="collapse"] {
 &::after {
  font-family: Flaticon;
  content: "\f10c";
  font-size: 0.8rem;
  float: right;
 }
}
/*ul*/.note {
  list-style: none;
  padding-left: 1.25rem;
  li {
    // line-height: 1.8rem;
    margin-bottom: 0.5rem;
    text-indent: -1rem;
    &::before {
      content: "";
      margin-right: 0.25rem;
    }
  }
}

.login-other-page {
  height: 100%;
  border-top: 1px solid darken(theme-color('light'), 20);
  padding-top: 2rem;
  @include media-breakpoint-up(sm) {
    border-top: none;
    border-left: 1px solid darken(theme-color('light'), 20);
    padding-top: 0;
  }
}
.fqa-list,.security-list{
  counter-reset:p;
  list-style: none;
  padding: 0;
  >p{
    position: relative;
    padding: .5em;
    margin-left: 2.5em;
    margin-top: 0;
    margin-bottom: 0;
    color: theme-color('light');
    background: theme-color('primary');
    &:before{
      content: counter(p);
      counter-increment: p;
      position: absolute;
      top: 0;
      bottom: 0;
      left: -2.5em;
      background:theme-color('primary');
      padding: .5em;
      width: 2.5em;
      border-right: 2px solid #fff;
      text-align: center;
      }
    }
  }
  
  .cursor-pointer {
    cursor: pointer;
  }
  