@font-face {
  font-family: "Flaticon";
  src: url(../images/font/Flaticon.eot);
  src: url(../images/font/Flaticon.eot?#iefix) format("embedded-opentype"),
       url(../images/font/Flaticon.woff) format("woff"),
       url(../images/font/Flaticon.ttf) format("truetype"),
       url(../images/font/Flaticon.svg#Flaticon) format("svg");
  font-weight: normal;
  font-style: normal;
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
  @font-face {
    font-family: "Flaticon";
    src: url(../images/font/Flaticon.svg#Flaticon) format("svg");
  }
}

